.pricing__section {
  padding: 48px 0 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #eee;
}

.pricing__container-card {
  /* background: linear-gradient(45deg, #0660e9 0%, #276afb 100%); */
  background-color: #6495ED;
  border: 7px solid #6495ED;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
  width: 18rem;
  height: 530px;
  text-decoration: none;
  border-radius: 4px;
  margin: 0 24px;
}

.pricing__container-card:hover {
  transform: scale(1.05);
  transition: all 0.3s ease-out;
  color: #1c2237;

}

.pricing__container {
  display: flex;
  justify-content: center;
  align-content: center;
}

.pricing__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.pricing__heading {
  color: #252e48;
  font-size: 3rem;
  padding: 0.3rem 0 2.5rem;
  /* margin-bottom: 54px; */
}

.pricing__container-cardInfo {
  display: flex;
  flex-direction: column;
  height: 500px;
  padding: 24px;
  align-items: center;
  color: #fff;
}

.pricing__container-cardInfo h3 {
  margin-bottom: 5px;
  font-size: 24px;
}

.pricing__container-cardInfo h4 {
  font-size: 40px;
}

.pricing__container-cardInfo p {
  font-size: 14px;
  margin-bottom: 24px;
}

.pricing__container-features {
  margin: 16px 0 32px;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pricing__container-features li {
  margin-bottom: 10px;
}

.icon {
  margin: 24px 0;
}

@media screen and (max-width: 1023px) {
  .pricing__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .pricing__container-card {
    width: 98%;
    margin: 24px;
  }

  .pricing__wrapper {
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .pricing__container-card:hover {
    transform: none;
  }
}

@media screen and (max-width: 414px) {
  .pricing__heading {
    font-size: 2rem;
  }
}

@media screen and (max-width: 320px) {
  .pricing__container-card {
    width: 100%;
  }

  .pricing__wrapper {
    margin: 0 30px;
  }

  .pricing__container-cardInfo {
    height: 500px;
    padding: 15px;
  }
  
  .pricing__container-cardInfo h3 {
    margin-bottom: 5px;
    font-size: 20px;
  }
  
  .pricing__container-cardInfo h4 {
    font-size: 35px;
  }
  
  .pricing__container-cardInfo p {
    font-size: 14px;
    margin-bottom: 24px;
  }

  .pricing__container-features li {
    margin-bottom: 10px;
    font-size: 15px;
  }
      
  .icon {
    margin: 24px 0;
  }
}

@media screen and (max-width: 280px) {
  .pricing__container-card {
    height: 450px;
  }

  .pricing__container-cardInfo {
    height: 500px;
    padding: 15px;
  }
  
  .pricing__container-cardInfo h3 {
    margin-bottom: 5px;
    font-size: 18px;
  }
  
  .pricing__container-cardInfo h4 {
    font-size: 30px;
  }
  
  .pricing__container-cardInfo p {
    font-size: 10px;
    margin-bottom: 24px;
  }
      
  .icon {
    margin: 24px 0;
  }

  .pricing__container-features li {
    margin-bottom: 10px;
    font-size: 12px;
  }
}