.access-wrapper {
  background-color: #eee;
  padding-bottom: 3rem;
  margin: 0 auto;
}

.access-map {
  padding: 0 5rem 1rem;
  align-items: center;
  width: 100%;
}

.access-map-text {
  font-size: 1.5rem;
  margin: 0.5rem auto 0;
  text-align: center;
}

.map-text {
  padding: 4rem 0 1.5rem;
}

@media screen and (max-width: 414px) {
  .access-map {
    padding: 0 1rem 1rem;
    align-items: center;
    width: 100%;
  }

}