.about-wrapper {
  background-color: #eee;
  color: #252e48;
}

.about-heading {
  padding: 3rem 0 2rem;
}

.about-heading > h1 {
  font-size: 3rem;
  text-align: center;
}

.about-text {
  max-width: 1120px;
  width: 83%;
  margin: 0 auto;
  border-top: 2px dashed black;
  border-bottom: 2px dashed black;
}

.about-text > h1 {
  font-size: 1.5rem;
  padding: 2rem 0 0;
  margin: 0;
  text-align: center;
}

.about-text > p {
  font-size: 1.2rem;
  padding: 1rem;
  margin: 0 1rem;
  font-weight: bold;
  text-align: left;
}

@media screen and (max-width: 540px) {
  .about-heading > h1 {
    font-size: 2rem;
    text-align: center;
  }

  .about-text {
    max-width: 95%;
  }

  .about-text > h1 {
    font-size: 1.2rem;
  }
  
  .about-text > p {
    font-size: 1rem;
    padding: 1rem 0;
    margin: 0 1rem;
    font-weight: bold;
    text-align: left;
  }
}

@media screen and (max-width: 320px) {
  .about-heading > h1 {
    text-align: center;
  }

  .about-text {
    max-width: 95%;
  }

  .about-text > h1 {
    font-size: 1rem;
  }
  
  .about-text > p {
    font-size: 0.8rem;
  }
}