.pricing-cautions {
  background-color: #eee;
  padding-bottom: 1.5rem;
  color: #252e48;
}

.pricing-cautions-text {
  border-top: 2px dashed black;
  padding: 3rem 0 1rem;
  max-width: 80%;
  margin: 0 auto;
}

.pricing-cautions-text > h3 {
  font-size: 1.2rem;
  text-align: left;
  margin-bottom: 0.5rem;
}

.pricing-cautions-text > h4 {
  color: red;
  font-size: 1.2rem;
  text-align: left;
  margin-bottom: 0.5rem;
}

.pricing-cautions-text > p {
  font-size: 1.1rem;
  font-weight: bold;
  margin: 1rem 0 0;
}

.contact-icons {
  font-size: 1.3rem;
  margin-right: 0.2rem;
}

@media screen and (max-width: 414px) {
  .pricing-cautions-text > h3 {
    font-size: 1.1rem;
  }
  
  .pricing-cautions-text > h4 {
    font-size: 1.1rem;
  }
  
  .pricing-cautions-text > p {
    font-size: 0.9rem;
  }
  
  .contact-icons {
    font-size: 1.3rem;
  }

  .pricing-cautions-text {
    max-width: 90%;
  }
}

@media screen and (max-width: 320px) {
  .pricing-cautions-text > h3 {
    font-size: 1rem;
  }
  
  .pricing-cautions-text > h4 {
    font-size: 1rem;
  }
  
  .pricing-cautions-text > p {
    font-size: 0.8rem;
  }
  
  .contact-icons {
    font-size: 1rem;
  }
}
