.cards {
  padding: 4rem;
  background: #eee;
  color: #252e48;
}

.cards > h1 {
  text-align: center;
  font-size: 3.5rem;
  margin-bottom: 2rem;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 100%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 2rem 0;
}

.cards__items {
  margin-bottom: 2rem;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 1rem;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 1.2rem;
  font-weight: 700;
  color: #fff;
  background-color: #6495ED;
  box-sizing: border-box;
  border-radius: 4px;
  opacity: 0.9;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 20px 30px;
}

.cards__item__text {
  color: #252e48;
  font-size: 1.2rem;
  line-height: 2rem;
}

/* @media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 100%;
  }
} */

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }

  .cards__item__pic-wrap::after {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 1023px) {
  .cards__items {
    margin-bottom: 3rem;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin-bottom: 3rem;
    border-radius: 10px;
  }
}

@media only screen and (max-width: 540px) {
  .cards > h1 {
    text-align: center;
    font-size: 2.4rem;
    margin-bottom: 1rem;
  }

  .cards__item__pic-wrap::after {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 414px) {
  .cards {
    padding: 3rem 2rem;
  }

  .cards > h1 {
    text-align: center;
    font-size: 2rem;
  }

  .cards__item__pic-wrap::after {
    font-size: 0.8rem;
  }

  .cards__item__text {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

@media only screen and (max-width: 414px) {
  .cards > h1 {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 320px) {
  .cards {
    padding: 3rem 1rem;
  }

  .cards > h1 {
    font-size: 1.4rem;
  }

  .cards__item__pic-wrap::after {
    font-size: 0.8rem;
  }

  .cards__item__text {
    font-size: 0.9rem;
    line-height: 1.4rem;
  }
}