.cat__contents-section {
  color: black;
  padding: 55px 0;
  background-color: #eee;
}

.cat__contents-row {
  align-items: center;
  padding-bottom: 30px;
  border-bottom: 2px dashed gray;
}

.cat__pictures-title > h1 {
  padding-top: 45px;
  text-align: center;
  font-weight: normal;
}

.cat__pictures-row {
  align-items:baseline;
  padding: 50px 0 30px;
  border-bottom: 2px solid gray;
}

.row {
  display: flex;
  margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap;
  align-content: stretch;
}

.col {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1;
  max-width: 50%;
  flex-basis: 50%;
}

.cat__contents-text-wrapper {
  max-width: 540px;
}

.cat__contents-text-wrapper > .heading {
  display: flex;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 8px;
  padding-left: 16px;
  font-size: 34px;
  font-weight: 500;
  color: black;
}

.cat__gender {
  padding-top: 0.3rem;
  margin-left: 1rem;
}

.male {
  color: rgb(47, 47, 252);
}
.female {
  color: rgb(252, 47, 47);
}

.cat__states {
  max-width: 100%;
  margin-bottom: 25px;
  font-size: 23px;
  line-height: 1.4;
  padding-left: 16px;
}

.cat__contents-subtitle {
  max-width: 100%;
  margin-bottom: 35px;
  font-size: 23px;
  line-height: 1.4;
  padding-left: 16px;
}

.cat__contents-img-wrapper {
  max-width: 555px;
}

.cat__contents-img {
  max-width: 95%;
  margin: 0 10px 0 0;
  padding-right: 0;
}

.cat__contents-img-wrapper > img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  border-radius: 10px;
}

.cat__back-btn {
  margin-top: 5rem;
  text-align: center;
}

@media screen and (max-width: 991px) {
  .container {
    padding-right: 30px;
    padding-left: 30px;
  }
  .cat__contents-text-wrapper > .cat__contents-subtitle {
    font-size:19px;
  }
}

@media screen and (max-width: 767px) {
  .cat__contents-text-wrapper {
    padding-bottom: 5px;
  }
  
  .col {
    max-width: 100%;
    flex-basis: 100%;
  }

  .cat__contents-text-wrapper > .cat__contents-subtitle {
    font-size: 17px;
  }
}

@media screen and (max-width: 320px) {
  .cat__contents-text-wrapper  > .heading{
    font-size:30px;
  }

  .cat__contents-text-wrapper > .cat__contents-subtitle {
    font-size: 14px;
  }
  
}