.form-container {
  margin: 0 auto;
  width: 100%;
  height: 100vh;
  /* display: flex; */
  /* display: grid; */
  /* position: relative; */
  background: #eee;
}

.form-heading {
  display: block;
  padding: 4rem 4rem 1rem;
  margin: 0 auto;
}

.form-heading > h1 {
  color: #252e48;
  font-size: 3rem;
  text-align: center;
}

.success-heading > h1 {
  color: #252e48;
  padding: 10rem 0;
  font-size: 2rem;
  text-align: center;
  margin: 2rem auto 0;
  max-width: 1000px;
  width: 90vw;
  background-color: rgb(149, 165, 209);
}

.form-success {
  text-align: center;
  color: #fff;
}

.form-content {
  width: 100%;
}

.form {
  max-width: 960px;
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.form-inputs {
  margin-bottom: 0.3rem;
  width: 60%;
}

.form-inputs p {
  font-size: 0.8rem;
  color: #f00e0e;
}

.form-label {
  display: inline-block;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #252e48;
}

.form-input {
  display: block;
  padding-left: 10px;
  outline: none;
  border-radius: 2px;
  height: 2.5rem;
  width: 100%;
  border: 1px solid #252e48;
}

.form-textarea {
  display: block;
  padding-top: 10px;
  padding-left: 10px;
  outline: none;
  border-radius: 2px;
  height: 10rem;
  width: 100%;
  border: 1px solid #252e48;
}

.form-input,.form-textarea::placeholder {
  color: #595959;
  font-size: 0.9rem;
}

.btn-wrapper {
  margin: 1.5rem 0 1rem;
}

.form-input-login {
  font-size: 0.9rem;
  color:#252e48;
  padding-bottom: 5rem;
  width: 80%;
  text-align: center;
}

@media screen and (max-width: 540px) {
  .form-container {
    height: 110vh;
  }

  .form-heading {
    padding: 2rem 4rem 1rem;
  }

  .form-heading > h1 {
    font-size: 2rem;
  }

  .form {
    height: 100vh;
  }

  .form-inputs {
    margin-bottom: 0.7rem;
    width: 90%;
  }

  .success-heading > h1 {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 376px) {
  .form-container {
    height: 110vh;
  }

  .form-heading {
    padding: 2rem 4rem 1rem;
  }

  .form-heading > h1 {
    font-size: 2rem;
  }

  .form {
    height: 100%;
  }

  .form-inputs {
    margin-bottom: 0.7rem;
  }

  .success-heading > h1 {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 320px) {
  .form-container {
    height: 120vh;
  }

  .form-inputs {
    margin-bottom: 0.3rem;
    width: 90%;
  }

  .success-heading > h1 {
    font-size: 1rem;
  }
}

@media screen and (max-width: 280px) {
  .form-container {
    height: 100vh;
  }

  .form-heading > h1 {
    font-size: 1.5rem;
  }

}