.home__contents-section {
  background-color: #eee;
  padding: 50px 0;
}

.home__contents-section-last {
  background-color: #eee;
  color: #fff;
  padding: 50px 0 150px;
}

.home__contents-row {
  align-items: center;
}

.row {
  display: flex;
  margin-right: -15px;
  margin-bottom: -15px;
  margin-left: -15px;
  flex-wrap: wrap;
  align-content: stretch;
}

.col {
  margin-bottom: 15px;
  padding-right: 15px;
  padding-left: 15px;
  flex: 1;
  max-width: 50%;
  flex-basis: 50%;
}

.home__contents-text-wrapper {
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
}

.top-line {
  color: #f00946;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  margin-bottom: 16px;
}

.home__contents-text-wrapper > .heading {
  margin-bottom: 24px;
  font-size: 40px;
  line-height: 1.1;
  font-weight: 600;
  color: #1c2237;
}

.home__contents-subtitle {
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  color: #1c2237;
}

.home__contents-img-wrapper {
  max-width: 555px;
}

.home__contents-img {
  max-width: 95%;
  margin: 0 0 0 10px;
  padding-right: 0;
}

.home__contents-img-wrapper > img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
  border-radius: 10px;
}

@media screen and (max-width: 991px) {
  .container {
    padding-right: 30px;
    padding-left: 30px;
  }

  .home__contents-text-wrapper > .heading {
    font-size: 25px;
  }
}

@media screen and (max-width: 760px) {
  .home__contents-text-wrapper {
    padding-bottom: 5px;
  }

  .home__contents-img {
    margin: 0;
  }
  
  .col {
    max-width: 100%;
    flex-basis: 100%;
  }
}

@media screen and (max-width: 320px) {
  .home__contents-text-wrapper  > .heading{
    font-size:24px;
  }
}