.access-wrapper {
  background-color: #eee;
  color: #252e48;
}

.access-heading {
  padding: 3rem 0 1rem;
}

.access-heading > h1 {
  font-size: 3rem;
  text-align: center;
}

.access-text {
  padding: 2rem 0;
  border-top: 2px dashed black;
  border-bottom: 2px dashed black;
  max-width: 90%;
  margin: 1.5rem auto 0;
}

.access-text > h3 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.access-text > p {
  font-size: 1.5rem;
  margin: 1.5rem 0 0;
}

.parking-info {
  font-size: 1rem;
}

@media screen and (max-width: 414px) {
  .access-text {
    padding: 2rem 0;
    border-top: 2px dashed black;
    border-bottom: 2px dashed black;
    max-width: 95%;
    margin: 1.5rem auto 0;
  }

  .access-heading > h1 {
    font-size: 2.5rem;
    text-align: center;
  }

  .access-text > h3 {
    text-align: center;
  }

  .access-text > p {
    font-size: 1.1rem;
    margin: 1.5rem 0 0;
  }
}