.hero-container {
  background: url('../../images/img-home.jpg') center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.1);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-size: 6rem;
  margin-top: 6rem;
}

.hero-container > p {
  width: 90%;
  margin-top: 0.7rem;
  color: #fff;
  font-size: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    margin-top: 4rem;
  }  
}

@media screen and (max-width: 769px) {
  .hero-container > h1 {
    font-size: 4rem;
    margin-top: 4rem;
  }
  
  .hero-container > p {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 540px) {
  .hero-container > h1 {
    font-size: 3rem;
  }
  
  .hero-container > p {
    font-size: 1rem;
  }
}

@media screen and (max-width: 414px) {
  .hero-container > h1 {
    font-size: 2.5rem;
    margin: 3rem 0 0;
  }
}

@media screen and (max-width: 375px) {
  .hero-container > h1 {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 320px) {
  .hero-container {
    justify-content: start;
  }

  .hero-container > h1 {
    font-size: 2rem;
  }

  .hero-container > p {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 280px) {
  .hero-container > h1 {
    font-size: 1.7rem;
  }

  .hero-container > p {
    font-size: 0.7rem;
  }
}