/* ↓precaution-last */
.precaution-last-container {
  background: url('../../images/img-last-precaution.jpg') center center/cover no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
  object-fit: contain;
}

.precaution-last-container > h1 {
  color: #fff;
  font-size: 1.4rem;
  margin-top: 2rem;
  width: 90%;
}

.precaution-last-container > p {
  width: 90%;
  margin-top: 8px;
  color: #fff;
  font-size: 2rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.precaution-last-container > .name {
  width: 90%;
  margin-top: 8px;
  color: #eee;
  font-size: 0.9rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

@media screen and (max-width: 1024px) {
  .precaution-last-container > h1 {
    font-size: 2rem;
    text-align: start;
  }  
}

@media screen and (max-width: 960px) {
  .precaution-last-container > h1 {
    font-size: 1.8rem;
    margin-top: 4rem;
  }  
}

@media screen and (max-width: 769px) {
  .precaution-last-container {
    text-align: center;
  }

  .precaution-last-container > h1 {
    margin-top: 4rem;
  }
  
  .precaution-last-container > p {
    font-size: 1.5rem;
  }
  
}

@media screen and (max-width: 415px) {
  .precaution-last-container {
    text-align: left;
  }
  
  .precaution-last-container > h1 {
    font-size: 0.9rem;
  }
  
  .precaution-last-container > .name {
    font-size: 0.5rem;
  }

}